.card-wrapper {
  display: flex;
  overflow-x: auto;

  &::-webkit-scrollbar {
    height: 5px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #bdbdbd;
  }
}

.categories-card {
  @include font-size(22);
  position: relative;
  margin-right: 35px;
  padding: 50px 20px 35px;
  min-width: 280px;
  color: $bg-txt;
  text-align: center;
  overflow: hidden;

  &:last-child {
    margin-right: 0;
  }

  &:before {
    content: '';
    width: 300px;
    height: 300px;
    border: 70px solid;
    border-radius: 50%;
    position: absolute;
    top: -130px;
    right: -130px;
  }

  button {
    text-transform: uppercase;
    padding: 15px;
    border-radius: 5px;
    border: 0;
    color: inherit;
    box-shadow: 0 0 2px rgba(0, 0, 0, .4);
  }

  @include media(min, 1224) {
    margin-right: 67px;
    padding: 100px 35px 55px;
    min-width: 420px;

    &:before {
      width: 440px;
      height: 440px;
      top: -200px;
      right: -150px;
    }

    button {
      padding: 20px;
    }
  }
}

.card-title {
  @include font-size(28);
  font-weight: $bold;
  position: relative;
  margin-bottom: 40px;
  text-transform: uppercase;

  &:after {
    content: '';
    transform: translateX(-50%);
    position: absolute;
    left: 50%;
    bottom: -20px;
    width: 80px;
    height: 3px;
  }

  @include media(min, 1224) {
    @include font-size(31);
    margin-bottom: 55px;

    &:after {
      bottom: -25px;
    }
  }
}

.card-txt {
  margin-bottom: 25px;
  position: relative;
}

.orange {
  background: $btn-color;

  &:before {
    border-color: #fc8128;
  }

  .card-title:after,
  button {
    background: $main-green;
  }
}

.green,
.yellow {
  .card-title:after,
  button {
    background: $btn-color;
  }
}

.green {
  background: $main-green;

  &:before {
    border-color: #84bb02;
  }
}

.yellow {
  background: $yellow;

  &:before {
    border-color: #fcd702;
  }
}
