.banner-slider {
  margin-top: 15px;
  background-image: linear-gradient(to right, #90cf00, #4fd25d, #00cf92, #00c9b8, #06c0cc);

  .container {
    padding-top: 40px;
    padding-bottom: 20px;
  }

  img {
    display: block;
    margin: 0 auto 25px;
    max-width: 200px;
    width: 100%;
  }

  @include media(min, 500) {
    .baner-item {
      display: flex!important;
      align-items: center;
      justify-content: center;
    }

    img {
      margin: 0 25px 0 0;
    }
  }

  @include media(min, 768) {
    img {
      max-width: 400px;
    }
  }

  @include media(min, 1024) {
    img {
      max-width: 600px;
    }
  }

  @include media(min, 1224) {
    .container {
      padding-top: 70px;
    }

     img {
       max-width: 800px;
     }
  }

  @include media(min, 1600) {
    img {
      max-width: none;
      width: auto;
    }

    .slick-prev,
    .slick-next {
      height: 60px;
      width: 60px;
      top: 50%;

      &:before {
        font-size: 60px;
      }
    }

    .slick-prev {
      left: -70px;
    }

    .slick-next {
      right: -70px;
    }
  }
}

.banner-slider-info {
  margin: 0 auto;
  max-width: 225px;
  color: $bg-txt;
  text-align: center;

  .desc {
    @include font-size(16);
    margin-bottom: 30px;
  }

  button {
    @include font-size(22);
    margin-bottom: 25px;
    padding: 15px 18px;
    border-radius: 5px;
    border: 0;
    background: $btn-color;
    box-shadow: 0 0 2px rgba(0, 0, 0, .4);
    color: inherit;
    text-transform: uppercase;
  }

  @include media(min, 500) {
    margin: 0;
    text-align: left;
  }
}

.previous-price {
  @include font-size(24);
  font-weight: $light;
}

.current-price {
  @include font-size(45);
  font-weight: $bold;
  margin-top: 5px;
  margin-bottom: 35px;
}


