// Set box sizing
*,
::after,
::before {
  box-sizing: border-box;
}

// Base styles
html,
body {
  font-size: $mob-font+px;

  @include media(min, 1224) {
    font-size: $desc-width-font+px;
  }
}

body {
  font-family: $roboto;
  min-height: 100vh;
  min-width: 320px;
  color: $dark-txt;
}

a,
button {
  outline: none;

  &:hover {
    cursor: pointer;
  }
}

a {
  text-decoration: none;
  transition: .2s ease;
  color: inherit;

  &:hover {
    text-decoration: underline;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;

  &:last-child {
    margin-bottom: 0;
  }
}

//Images style
img {
  max-width: 100%;
  height: auto;
  display: inherit;
}

// Lists style
ul, ol, dl {
  margin: 0;
  padding: 0;
  list-style: none;

  li {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

// Page inner container
.container {
  max-width: 1440px;
  padding: 0 20px;
  margin: 0 auto;
}

// General style
section {
  margin: 70px 0;
}

.no-scroll {
  overflow: hidden;
}

