/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  //top: 50%;
  bottom: 10px;
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
  z-index: 1;

  &:hover,
  &:focus {
    outline: none;
    background: transparent;
    color: transparent;

    &:before {
      opacity: 1;
    }
  }

  &.slick-disabled:before {
    opacity: .25;
  }

  &:before {
    font-family: "slick";
    font-size: 30px;
    line-height: 1;
    color: $bg-txt;
    //opacity: .75;
    //-webkit-font-smoothing: antialiased;
    //-moz-osx-font-smoothing: grayscale;
  }
}

